
@use "@styles/variables" as *

.footer
  margin: 0
  padding: 0
  background: $dark-blue-gradient

.footer__container
  display: flex
  justify-content: space-between
  align-items: center
  height: inherit
  margin: auto
  padding: 60px 0

.footer__social-list
  display: flex
  justify-content: space-between
  align-items: center
  gap: 30px
  list-style: none
  margin: 0
  padding: 0

.footer__social-link
  display: block
  width: 35px
  height: 35px
  background-color: $white-light
  mask-repeat: no-repeat
  mask-position: center
  mask-size: contain

  &:hover
    opacity: 0.8

  &:active
    opacity: 0.7

.footer__tel-link
  font-weight: 100
  font-size: 28px
  line-height: 33px
  color: $white
